body{font-family:Circular, -apple-system, BlinkMacSystemFont, Roboto, Helvetica Neue, sans-serif;font-size:14px;line-height:1.43;color:#333333;margin:0;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;box-sizing:border-box;background-color:#f7f7f7 !important}html{box-sizing:border-box;-webkit-text-size-adjust:100%}code{font-family:source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace}*{box-sizing:border-box}.spinner{width:200px;height:200px;line-height:200px;text-align:center;position:absolute;z-index:9999;top:0;left:0;bottom:0;right:0;margin:auto;color:#10293f;font-weight:bold}.singleViewCard{padding:30px;border:3px solid #e7e7e7;background-color:#f7f7f7;border-radius:10px;margin:50px auto;width:30%}.singleViewCard .singleView{height:30px;line-height:30px;margin:5px 0}.singleViewCard span{font-weight:600;color:#10293f}.singleViewCard .goback{padding:5px 20px;background:#10293f;color:#fff;margin:20px 0;border-radius:5px;display:block;width:150px;text-align:center}.singleViewCard .singleViewTitle{font-size:16px;font-weight:bold;border-bottom:1px solid #333;padding:0px 0 20px 0;margin:0 0 20px 0}::-webkit-scrollbar{width:10px}::-webkit-scrollbar-track{background:#69ba9a}::-webkit-scrollbar-thumb{background:#10293f}::-webkit-scrollbar-thumb:hover{background:#1f507a}header{background-color:#10293f;width:100%;height:60px;position:relative;top:0;left:0;padding:10px 20px;text-align:left}header img.logo{height:100%}.dataTable{margin:20px;background:#fff;margin:0px 30px;box-sizing:border-box;min-height:calc(100vh - 60px);padding:20px}.dataTable .companyDetails{height:60px;font-size:31px;font-weight:bold;margin:0 0 30px 0;border-bottom:10px solid #fafafa;padding:0 0 70px 0;color:#10293f}.dataTable .search{color:#10293f;padding:10px;margin:0 0 30px 0;border:1px solid grey}.dataTable table tr td{color:#333333}
